
import MenuDots from "@/components/MenuDots.vue";

export default {
	name: "Card",
	components: { MenuDots },
	props: {
		title: String,
		menu: Array as () => string[],
		like: {
			type: Boolean,
			default: undefined
		},
		custom: {
			type: String,
			default: undefined
		}
	},
	emits: ["click:like", "click:menu"]
};
